<template>
  <div class="geren">
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      left-arrow
      @click-left="back"
    />
    <div class="zegai"></div>
    <div class="grs"  >
        <van-cell title="头像" >
         <template #right-icon>
            <img class="tx" :src="gerenzl.userImg">
        </template>
        </van-cell>
        <van-cell border title="用户账号"  :value="gerenzl.phone"/>
        <van-cell title="用户ID"  :value="gerenzl.userId"/>
        <van-cell title="昵称"  :value="gerenzl.nickName"/>
         <van-field v-model="value.nickName" 
         input-align="right" 
         label="修改昵称" 
         placeholder="请输入修改的昵称" 
         @change="name"
        />

         <van-field v-model="value.desc" 
         type="textarea" 
         autisize
         input-align="right" 
         show-word-limit
         label="简介" 
         placeholder="请输入简介" 
        @change="jianjie"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "Geren",
  data() {
    return {
        gerenzl:{},
        value:{
          nickName:'',
          desc:'',
          phone:'',
          userId:'',
        },

    };
  },

  created () {
    this.gerens();
  },

  methods: {

    back() {
      this.$router.go(-1);
    },

    gerens() {
      let tokenString = localStorage.getItem("tokenString");
      

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "GET",
        url: this.baseUrl + "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code === 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === "B001") {
            this.gerenzl = res.data.result[0];
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
    
    //修改昵称
    name(){
        let tokenString = localStorage.getItem("tokenString");
      

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/updateNickName",
        data: {
          appkey: this.appkey,
          tokenString,
          nickName:this.value.nickName
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code === 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === "C001") {
              if (res.data.result[0] === 1) {
              this.gerenzl.nickName = res.data.nickName;
            }
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },


    //个人简介
    jianjie(){
        let tokenString = localStorage.getItem("tokenString");
      

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/updateDesc",
        data: {
          appkey: this.appkey,
          tokenString,
          desc:this.value.desc
        },
      })
        .then((res) => {
          
          this.$toast.clear();

          if (res.data.code === 700) {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
            return this.$router.push({ name: "Login" });
          }

          if (res.data.code === "D001") {
            this.jianjies = res.data;
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    }
  },
};
</script>

<style lang="less" scoped>
.geren {
  .zegai {
    width: 100%;
    background: #f6c73d;
    height: 120px;
  }
  .grs{
      width: 95%;
      position: relative;
      top: -25px;
      left: 9px;
      .tx{
          width: 30px;
          height: 30px;
          border-radius: 50%;
      }
      .ps{
          width: 140px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
      }
  }
}
</style>